body {
  margin: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

$durationSecond: 60s;
$durationMin: 60s * 60;

.stopwatch {
  display: flex;
  justify-content: center;

  border-radius: 100%;
  width: 80vmin;

  height: 80vmin;
  border: 1vmin dotted #000;

  box-sizing: border-box;

  &__minutes {
    margin-top: 19vmin;
    height: 20vmin;

    width: 3vmin;
    background-color: #0700ff;

    animation-iteration-count: infinite;
    animation-duration: $durationMin;

    transform-origin: bottom;
    animation-timing-function: steps(60);

    animation-name: min2;
  }

  &__seconds {
    position: absolute;
    transform-origin: bottom;

    height: 38vmin;
    width: 1.5vmin;

    margin-top: 8px;
    background-color: #2c8000;

    animation-name: secon;
    animation-duration: $durationSecond;

    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  &__circle {
    width: 5vmin;
    height: 5vmin;

    border-radius: 100%;
    background-color: #f6a603;
    position: absolute;
    align-self: center;
  }

  &--speed-up {

    .stopwatch__second {
      animation-duration: 1s;
    }

    .stopwatch__minutes {
      animation-duration: 10 * 10s;
    }

  }
}

@keyframes min2 {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes secon {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
