body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  box-sizing: border-box;
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-top: 19vmin;
  animation-name: min2;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-top: 8px;
  animation-name: secon;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 100%;
  align-self: center;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__second {
  animation-duration: 1s;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 100s;
}

@keyframes min2 {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes secon {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
